<template>
  <div id="Message">
    <article class="a1">
      <h2>ÁïôË®ÄÊùø</h2>
      <p class="des">
        ËØ∑ÊñáÊòéÂèëË®Ä,Á¶ÅÊ≠¢ÂπøÂëä„ÄÇ‰∏çÁÑ∂ÊãâÈªë‰∫ÜÂó∑üåû~
      </p>
      <el-input
        type="textarea"
        :row="2"
        placeholder="ËØ∑ËæìÂÖ•ÂÜÖÂÆπ"
        v-model="textarea"
        maxlength="100"
        show-word-limit
      ></el-input>
        <div class="btn">
          <div class="left">
            <i class="iconfont icon-biaoqing" @click.stop="showEmoji"></i>
            <div class="emoji" v-if="ifShowEmoji">
              <ul>
                <li v-for="item in emoji" 
                :key="item"
                @click.stop="clickEmoji(item)"
                >{{item}}</li>
              </ul>
            </div>
          </div>
          <el-button 
            type="primary"
            size="mini"
            @click="submit"
            >ÂèëË°®</el-button>
        </div>
    </article>
    <article class="a2">
      <ul>
        <li v-for="item in msgList" :key="item._id">
          <div class="parent">
            <div 
            class="left"
            :style="{backgroundImage:`url(${baseURL}${item.author.photo})`}"
            ></div>
            <div class="right">
              <div class="name-time ">
                <span>{{item.author.user}}</span>
                <span>{{formatDate(item.date)}}</span>
              </div>
              <div class="message">
                {{item.text }}
              </div>
              <div class="like-reply">
                <i class="like iconfont icon-xinaixin "
                :class="{isLike:userInfo._id && item.likes.includes(userInfo._id)}"
                @click="parentLikeClick(item._id)"></i>
                <span>{{item.likes.length}}</span>
                <i class="reply iconfont icon-huifu"
                @click="parentReplyClick(item)"></i>
              </div>
            </div>
          </div>
          <div class="child" v-for="(childItem,cindex) in item.children" :key="childItem._id">
            <div 
            class="left"
            :style="{backgroundImage:`url(${baseURL}${childItem.author.photo})`}"
            ></div>
            <div class="right">
              <div class="name-time ">
                <span>{{childItem.author.user}}</span>
                <span>2{{formatDate(childItem.date)}}</span>
              </div>
              <div class="message">
                <span>@{{childItem.replyUser.user}}</span>{{childItem.text}}
              </div>
              <div class="like-reply">
                <i class="like iconfont icon-xinaixin"
                :class="{isLike:userInfo._id&&childItem.likes.includes(userInfo._id)}"
                @click="childLikeClick(item._id,childItem._id,cindex)"
                ></i>
                <span>{{childItem.likes.length}}</span>
                <i class="reply iconfont icon-huifu"
                  @click="childReplyClick(item,childItem)"
                ></i>
              </div>
            </div>
          </div>
          <div class="replyInput "
          :class="{show:item.ifShowReply}"
          >
            <el-input size="mini" 
            :placeholder="`@${item.replyUser.user}`"
            v-model="item.replyTxt"
            ></el-input>
            <el-button 
              type="primary"
               size="mini"
               @click="replySubmit(item)">ÁïôË®Ä</el-button>
          </div>
        </li>
      </ul>
    </article>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
    name:"Message",
    data(){
      return{
        //ÁïôË®ÄÂÜÖÂÆπ
        textarea:"",
        //Áî®‰∫éÊòæÁ§∫/ÈöêËóè emojiÈÄâÊã©Ê°Ü
        ifShowEmoji:false,
        //emojiÊï∞ÁªÑ
        emoji:[
          "üòÄ","üòÅ","üòÇ","ü§£","üòÉ","üòÑ","üòÖ","üòÜ","üòâ","üòä","üòã",
          "üòé","üòç","üòò","ü•∞","üòó","üòô","ü•≤","üòö","üôÇ","ü§ó","üéà",
          "ü§©","ü§î","ü§®","üòê","üòë","üò∂","üôÑ","üòè","üò£","üò•","üòÆ",
          "ü§ê","üòØ","üò™","üò´","ü•±","üò¥","üòå","üòõ","üòú","üòù","ü§§",
          "üòí","üòì","üòî","üòï","üôÉ","ü§ë","üòû","üòü","üò≤","‚òπÔ∏è","üò§",
          "üò¢","üò≠","üôÅ","üò¶","üòñ","üò∞","üò¨","ü§Ø","üò©","üò®","üòß",
          "üò±","ü•µ","ü•∂","üò≥","ü§™","üòµ","ü§í","üò∑","ü§¨","üò°","üò†",
          "ü•¥","ü§ï","ü§¢","ü§Æ","ü§ß","üòá","ü•≥","ü§´","ü§•","ü§°","ü§†",
          "ü•∫","ü•∏","ü§≠","üßê","ü§ì","üòà","üëø","üëπ","üë∫","üíÄ","‚ò†Ô∏è",
          "üëª","üëΩ","üí™","ü¶µ","ü¶∂","üëÇ","ü¶ª","üëÉ","ü§è","üëà","üëå",
          "üëâ","‚òùÔ∏è","üëÜ","üëá","‚úåÔ∏è","ü§û","üññ","ü§ò","ü§ô","üñêÔ∏è","‚úã",
          "ü§å","üëç","üëé","‚úä","üëä","‚úçÔ∏è","ü§ù","üíÖ","üëè","ü§õ","ü§ú",
          "ü§ö","üëã","ü§ü","üôè","ü§≤","üôå","üëê","üíù","üíò","üíñ","üíó",
          "üíì","üíû","üíï","‚ù£Ô∏è","üíî","‚ù§Ô∏è","üß°","üíü","üíå","üí¢","üîû"
        ],
        //ÊâÄÊúâÁïôË®ÄÂàóË°®
        msgList:[

        ]
      }
    },
    computed:{
      ...mapState(['userInfo'])
    },
    methods:{
      //ÊòæÁ§∫Ë°®ÊÉÖÊ°Ü
      showEmoji(){
        this.ifShowEmoji = !this.ifShowEmoji
      },
      //Ë°®ÊÉÖÂõæÊ†áÁÇπÂáª
      clickEmoji(item){
        console.log(this.textarea.length);
        if(this.textarea.length>98)return
        
        this.textarea+=item
      },
      //Êèê‰∫§ÁïôË®Ä ÂÜôÂú®methodsÁöÑÂáΩÊï∞ÂèØ‰ª•Áõ¥Êé•Âä†async
      async submit(){
        //Ê≤°ÊúâÁôªÈôÜÁöÑËØù ÁªôÊèêÁ§∫
        //console.log(this.userInfo);
        if(!this.userInfo.user){
          return this.$message.error("ËØ∑ÂÖàÁôªÂΩï")
        }
        //ÁïôË®ÄÂÜÖÂÆπ‰∏∫Á©∫
        let val = this.textarea.trim()
        if(!val){
          return this.$message.error("ËØ∑ËæìÂÖ•ÂÜÖÂÆπ")
        } 

        //ÂèëÈÄÅÂà∞ÂêéÁ´Ø
        let {data} = await this.$axios({
          method:"POST",
          url:"/msg/submit",
          data:{
            val
          }
        })
        //ÁïôË®ÄÂ§±Ë¥•
        if(data.code){
          return $message.error(data.msg)//ÊèêÁ§∫
        }
        //ÁïôË®ÄÊàêÂäü
        this.$message.success("ÁïôË®ÄÊàêÂäüÂï¶")//ÊèêÁ§∫
        this.textarea = ""//ÂÜÖÂÆπÊ∏ÖÁ©∫
        this.getMsg()
      },
      //ËØ∑Ê±ÇÊâÄÊúâÁïôË®Ä
      async getMsg(){
          let {data} = await this.$axios({
            method:"GET",
            url:"/get/msg"
          })
          let d = data.data
          d.forEach(item=>{
            item.ifShowReply = false//Áî®‰∫éÊéßÂà∂ÂõûÂ§çÊ°ÜÊòæÁ§∫/ÈöêËóè
            item.replyTxt=""// ÂõûÂ§çÊ°ÜÁöÑÂÜÖÂÆπÁªëÂÆö
            item.replyUser=item.author//ÊèêÁ§∫Ê°ÜÊòæÁ§∫ÊñáÊú¨ÂÜÖÂÆπ
          })
          console.log(d);
          this.msgList = d
      },
      //ËΩ¨Êç¢Êó•ÊúüÊòæÁ§∫
      formatDate(date){
        let d = new Date(date),
        YY = d.getFullYear(),
        MM = d.getMonth()+1,
        DD = d.getDate(),
        hh = d.getHours(),
        mm = d.getMinutes(),
        ss = d.getSeconds()
        MM = (MM < 10? "0":"") +MM
        DD = (DD < 10? "0":"") +DD
        hh = (hh < 10? "0":"") +hh
        mm = (mm < 10? "0":"") +mm
        ss = (ss < 10? "0":"") +ss
        return `${YY}-${MM}-${DD} ${hh}:${mm}:${ss}`
      },
      //Áà∂Á∫ßÁÇπËµûÊåâÈíÆ
      async parentLikeClick(id){
        //Âà§Êñ≠Áî®Êà∑ÊòØÂê¶ÁôªÂΩï
        if(!this.userInfo.user){
          return this.$message.error("ËØ∑ÂÖàÁôªÂΩï")
        }
        let {data} = await this.$axios({
            method:"POST",
            url:"/msg/like/parent",
            data:{
              id//ËØÑËÆ∫id
            }
        })
        console.log(data);
        //Êìç‰ΩúÂ§±Ë¥•
        if(data.code){
          return this.$message.error(data.msg)
        }
        //Êìç‰ΩúÊàêÂäü
        this.getMsg()
      },
      //ÂõûÂ§çÊåâÈíÆÁöÑÁÇπÂáª
      parentReplyClick(item){
        //Âà§Êñ≠Áî®Êà∑ÊòØÂê¶ÁôªÂΩï
        if(!this.userInfo.user){
          return this.$message.error("ËØ∑ÂÖàÁôªÂΩï")
        }
        //ÈÄöËøáÂà§Êñ≠@ÁöÑÂêçÂ≠ó‰∏ÄÊ†∑‰∏ç‰∏ÄÊ†∑ Êù•ÂÜ≥ÂÆöÊòØÂê¶Â±ïÂºÄ„ÄÅ
        if(item.author._id !==item.replyUser._id){
          item.ifShowReply = true
        }else{
          //ÊéßÂà∂ÊòæÁ§∫ÈöêËóè
        item.ifShowReply =!item.ifShowReply
        }
        //ÊîπÂèò@ÂêéÈù¢ÁöÑÂêçÂ≠ó
        item.replyUser = item.author
      },
      //ÂõûÂ§çÁöÑÊèê‰∫§
      async replySubmit(item){
        //Âà§Êñ≠Áî®Êà∑ÊòØÂê¶ÁôªÂΩï
        if(!this.userInfo.user){
          return this.$message.error("ËØ∑ÂÖàÁôªÂΩï")
        }
        let text = item.replyTxt.trim()
        //È™åËØÅÊï∞ÊçÆÊ†ºÂºè
        if(!text){
          return this.$message.error("ËØ∑ËæìÂÖ•ÂõûÂ§çÂÜÖÂÆπ")
        }
        if(text.length>100){
          return this.$message.error("ÂõûÂ§çÂ≠óÊï∞‰∏çËÉΩË∂ÖËøá100")
        }

        //ÂèëÈÄÅÊï∞ÊçÆÂà∞ÂêéÁ´Ø
        let {data} = await this.$axios({
          method:"POST",
          url:"/msg/reply/submit",
          data:{
            id:item._id,
            text,
            replyUser:item.replyUser._id
          }
        })
        if(data.code){
          return this.$message.error(data.msg)
        }
        this.$message.success("ÂõûÂ§çÊàêÂäü")
        this.getMsg()
      },
      //Â≠êÂõûÂ§çÁÇπËµû
      async childLikeClick(pid,cid,cindex){
        //Âà§Êñ≠Áî®Êà∑ÊòØÂê¶ÁôªÂΩï
        if(!this.userInfo.user){
          return this.$message.error("ËØ∑ÂÖàÁôªÂΩï")
        }
        //ÂèëÈÄÅËØ∑Ê±Ç
        let {data} = await this.$axios({
          method:"POST",
          url:"/msg/like/child",
          data:{pid,cid,cindex}
        })
        //console.log(data);

        if(data.code){
          return this.$message.error(data.msg)
        }
        this.getMsg()
      },
      //Â≠êÂõûÂ§çÊåâÈíÆÁÇπÂáª
      childReplyClick(item,cItem){
        //Âà§Êñ≠Áî®Êà∑ÊòØÂê¶ÁôªÂΩï
        if(!this.userInfo.user){
          return this.$message.error("ËØ∑ÂÖàÁôªÂΩï")
        }
        //ÈÄöËøáÂà§Êñ≠@ÁöÑÂêçÂ≠ó‰∏ÄÊ†∑‰∏ç‰∏ÄÊ†∑ Êù•ÂÜ≥ÂÆöÊòØÂê¶Â±ïÂºÄ„ÄÅ
        if(cItem.author._id !==item.replyUser._id){
          item.ifShowReply = true
        }else{
          item.ifShowReply = !item.ifShowReply
        } 
        //ÊîπÂèòreplyUser
        item.replyUser = cItem.author
        
      }
  },
    mounted(){
      //ÁÇπÂáªdocument emojiÊ∂àÂ§±
      document.addEventListener("click",()=>{
        this.ifShowEmoji = false
      })
    },
    created(){
      //ËØ∑Ê±ÇÁïôË®ÄÊï∞ÊçÆ
      this.getMsg()
      console.log(111);
    }
}
</script>

<style lang="less" scoped>
#Message{
  article {
        box-sizing: border-box;
        width: 100%;
        margin-bottom: 15px;
        box-shadow: 0 0 4px #ddd;
        padding: 25px 20px ;
        background-color: #fff;
        font-family: Quicksand,Microsoft YaHei,sans-serif;
        h2 {
            margin-bottom: 15px;
            font-size: 20px;
            letter-spacing: 3px;
            line-height: 24px;
            border-left: 5px solid #73b899;
            text-indent: 10px;
        }
        p {
            text-indent: 2em;
            color: #444;
            font-size: 14px;
            letter-spacing: 4px;
            line-height: 30px;
        } 
    }
  article.a1{
    .el-textarea {
          margin-top: 15px;
    }
  /* Ê†∑ÂºèÁ©øÈÄè */
  /deep/ textarea{
          font-family: "Quicksand","ÂæÆËΩØÈõÖÈªë";
          letter-spacing: 5px;
          height: 70px;
          font-size: 12px;
    }
    .btn{
        display: flex;
        margin-top: 10px;
        justify-content: space-between;
        .left{
          position: relative;
        }
        i{
          font-size: 20px;
          cursor:pointer;
      }
      .emoji{
        position: absolute;
        top:30px;
        background-color: #fff;
        padding: 8px;
        z-index: 10;
        box-shadow: 0 0 3px #aaa;
        &::before{
          content: "";
          position: absolute;
          z-index: 3;
          top: -10px;
          left: 5px;
          width: 0;
          height: 0;
          border: 5px solid transparent;
          border-bottom-color: #ccc;
        }
        ul{
          display: flex;
          flex-wrap: wrap;
          width: 275px;
          border-left: 1px solid #e5e5e5;
          border-bottom: 1px solid #e5e5e5;
          li{
            box-sizing: border-box;
            float: left;
            width: 25px;
            height: 25px;
            border-right: 1px solid #e5e5e5;
            border-top: 1px solid #e5e5e5;
            font-size: 18px;
            line-height: 25px;
            cursor: pointer;
          }
        }
      }
    }
  }
  article.a2{
    ul li{
      padding-bottom: 5px;
      margin-bottom: 10px;
      border-bottom: 1px dashed #ddd;
      font-size: 12px;
    .child{
      margin-left: 55px;
      margin-top: 3px;
      }
    .parent,.child{
      display: flex;
    .left{
      overflow: hidden;
      width: 40px;
      height: 40px;
      margin-right: 15px;
      border-radius: 50%;
      background: none top center/cover;
    }
    .right{
      flex: 1;
      .name-time{
        font-size: 12px;
        margin-bottom: 3px;
        span:nth-child(1){
          color:#73b899
        }
        span:nth-child(2){
          color:#aaa;
          margin-left: 10px;
        }
      }
      .message{
        color: #666;
        letter-spacing: 2px;
        margin-bottom: 3px;
        word-break: break-word;
        span{
          color: #409eff;
          margin-right: 5px;
        }
      }
      .like-reply{
         i{
           cursor: pointer;
           font-size: 14px;
           color: #aaa;
         }
         i.isLike{
           color:#f30606;
         }
         span{
           display: inline-block;
           width: 43px;
           font-size: 12px;
           color: #aaa;
         }
      }
    }
      }
    .replyInput{
      overflow: hidden;
      transition: height .3s;
      height: 0px;
      width: 100%;
      padding-left: 55px;
      box-sizing: border-box;
      margin-top: 10px;
      &.show{
        height: 57px;
      }
      .el-button{
        padding: 0px 9px!important;
        float: right;
      }
    }
    }
  }
}
   
</style>